<template>
  <div class="result-container" v-loading.fullscreen.lock="loading">
    <div>方案编号：{{result.proCode}}</div>
    <div class="title">
      油改水定制涂装体验方案
    </div>
    <h4>Part1: 油改水基本需求信息</h4>
    <div class="info-container client">
      <h5>客户名称: {{result.customer.company || ''}}</h5>
      <h5>联系人: {{result.customer.name}}</h5>
      <h5>联系电话: {{result.customer.phone}}</h5>
    </div>
    <div class="info-container product">
      <h5>家具风格: {{productStyle}}</h5>
      <h5>家具类型: {{productType}}</h5>
      <h5>家具底材: {{result.product.base}}</h5>
      <h5>涂装效果: {{productEffect}}</h5>
      <h5>涂料种类: {{paintingType}}</h5>
      <h5>面漆光泽: {{productLuster}}</h5>
      <h5>涂装工件类型: {{productWp}}</h5>
      <h5>水性漆希望采取的涂装方式: {{productProgram}}</h5>
    </div>
    <div class="info-container">
      <h4>Part2：水性漆涂装建议方案</h4>
      <h5>2.1水性漆产品推荐</h5>
      <data-table :tableData="paintingTable.data" :columns="paintingTable.columns" :canMerge="true"></data-table>
      <div class="hint">说明：工艺推荐仅供参考，可结合实际涂装效果进行调整！</div>
    </div>
    <div class="info-container">
      <h5>2.2设备涂装及干燥方式建议</h5>
      <data-table :tableData="programTable.data" :columns="programTable.columns" :canMerge="false"></data-table>
      <div class="hint">设备成本以国产设备为标准，价格仅供参考</div>
    </div>
    <div class="info-container">
      <h4>Part3：“油转水”必备知识点</h4>
      <h5>3.1 为什么封闭对水性木器漆的涂装如此重要？</h5>
      <p>水是水性木器漆的主要成份之一，涂装后木材纤维容易吸水发涨；而让木材表层的游离水、湿膜里的水按理想的方式干净地挥发掉，又是一个复杂的综合过程。封闭环节对水性木器漆涂装效果的影响远远大于溶剂型木器漆，因此，封闭是涂装中首先要重点解决的问题。</p>
      <p>封闭的作用包括：</p>
      <p>1、能有效去除木毛、木刺，使木材表面更加平整、光滑；</p>
      <p>2、防止或减少水向下渗的问题，使干燥过程更顺利、使最终效果更理想；</p>
      <p>3、起到“托起”和“带路”的作用，便于后续涂装的顺利进行；</p>
      <p>4、有效地封闭木材的水分、油脂、单宁酸，减少涂装的困扰。</p>
      <p>水性漆器漆涂装主要采用的封闭漆种类包括：单组分水性漆、双组分水性漆、PU/NC封闭底漆。</p>
      <h5>3.2 水性木器漆涂装过程中干燥的难点是什么？</h5>
      <p>在水性木器漆的涂装过程中，干燥是最不确定的因素，也是最难控制的因素。在所有涂料品种中，干燥对水性木器漆市场推广的影响是最大的。具体原因如下：</p>
      <p>1､水比溶剂更容易被木材吸收；</p>
      <p>2､水比溶剂更难于向上挥发掉；</p>
      <p>3､水的挥发比溶剂需要更多的能量。</p>
      <p>水性木器漆干燥的理想过程是由内而外地进行干燥，因此，希望表干不要太快。如果湿膜表干过快，会极大地增加了后续水分挥发的难度，导致水性木器漆成膜不好，进而影响最终的涂装效果。水性木器漆的干燥需要多种干燥方式有效的组合，需要很好地顾及众多条件的同时使水分干净地走掉！一般而言，抽湿会比升温显得更为有效，稍热的干风是理想的助干媒介。目前采用的干燥的方式有：自然干燥、热风干燥、红外干燥、微波干燥、紫外光干燥。</p>
      <h5>3.3 水性木器漆的色彩表现与溶剂型木器漆有什么不同？</h5>
      <p>水性木器漆是以水为分散介质的一种涂料品种，与溶剂型涂料相比，水性木器漆更难以表现出高饱和度、艳丽的色彩。因此，水性木器漆应重点推介淡雅、幽静、浅色、中低光泽的色彩效果！</p>
      <p>水性木器漆对漆膜色彩的影响因素包括：</p>
      <p>1､饱和度降低，影响色彩鲜艳度的表现；</p>
      <p>2､对颜色的调配、储存、使用要求更高；</p>
      <p>3､在干燥过程中表现为：湿深干浅，会变浅；</p>
      <p>4､色彩的层次感表现力减弱。</p>
      <h5>3.4 千万别忽视基材对水性木器漆涂装的影响。</h5>
      <p>底好面才好，要想获得理想的涂装效果，首先在基材的选择和处理上就需要认真对待，如在基材的含水率、粘结以及贴皮所采用的胶水等都将直接影响最终的涂装效果。</p>
      <p>1、木材的含水率≤12%，最好能控制在10%以内；</p>
      <p>2、木材粘结或贴皮所选用的胶水必须要具有良好的耐水性，防止涂装水性漆后胶水的粘结性降低。</p>
      <h5>3.5 如何才能合理配套及应用水性木器漆？</h5>
      <p>一、选择水性木器漆时需要考虑的因素：</p>
      <p>1、涂装效果可重复实现性；</p>
      <p>2、漆膜表面物理化学性能是否符合要求；</p>
      <p>3、涂装工序合理性评估；</p>
      <p>4、水性漆干燥速度对生产周期的影响；</p>
      <p>5、批量生产稳定性；</p>
      <p>6、涂装综合成本合理。</p>
      <p>二、应用水性木器漆时需要注意的事项：</p>
      <p>1、使用前充分搅拌均匀，尤其是添加的水性固化剂更应如此；</p>
      <p>2、喷涂空间要达到无尘；</p>
      <p>3、涂布量要合适，建议≤120g/m2；</p>
      <p>4、操作、干燥区温湿度稳定，层间充分干燥；</p>
      <p>5、底面色比例合理分配；</p>
      <p>6、与水性漆接触的器具要防锈。</p>
      <h5>3.6 水性木器漆涂装设备的采购是一个系统的工程。</h5>
      <p>“油转水“的关键不只是将油性漆改为水性漆，还需要对涂装设备的合理改造，才能最终达到”油转水“的目的。因此，在选购涂装设备时需要系统考量以下几点：</p>
      <p>1、设备选型：根据产品的结构特点来选择；</p>
      <p>2、工作效率：根据订单的规模和满足的效率来确定；</p>
      <p>3、配套组合：不同设备之间的相互搭配合理性；</p>
      <p>4、综合成本：综合考虑设备的采购成本、使用成本以及日常维护成本；</p>
      <p>5、排阵布线：设备最终在车间的的安装布线可行性，如：车间的面积、高度、密闭等环境因素；</p>
      <p>6、绿色环保：采用干式喷房或设备进行涂装，密闭空间施工，监控排放，三废统一收集处理。</p>
    </div>
    <div class="info-container">
      <h4>Part4：华润水性漆服务方案</h4>
      <img src="../../static/image/process.png" alt="服务方案">
    </div>
    <div class="info-container">
      <p>如需进一步了解咨询请与如下人员取得联系！</p>
      <p>办 事 处：XXX</p>
      <p>联 系 人：XXX</p>
      <p>手机号码：139XXXXXXXX</p>
    </div>
    <div class="controller" id="no-print">
      <el-button type="primary" @click="print" icon="el-icon-printer">打印结果</el-button>
      <el-button @click="goToIndex">返回首页</el-button>
    </div>
  </div>
</template>

<script>
import {
  PAINTING_RESULT,
  PAINTING_COLUMN,
  DRY_PROGRAM,
  DEVICE_PROGRAM,
  DEVICE_COLUMNS,
  DRY_COLUMNS,
  MAPS
} from '../constants/infomation'
import DataTable from '@components/DataTable'
import { join } from 'lodash'
import axios from 'axios'
// const MOCK_RESULT = {
//   proCode: '18013035',
//   customer: {
//     company: '深圳市大疆创新科技有限公司',
//     name: 'hello',
//     phone: '13160822222'
//   },
//   product: {
//     base: 22,
//     effect: 12,
//     luster: '1000',
//     program: 0,
//     style: 2,
//     type: 8,
//     workpiece: 4
//   },
//   program: {
//     method: '0, 4',
//     painting: 5
//   }
// }

export default {
  name: 'result',
  data () {
    return {
      loading: true,
      result: {
        customer: {},
        product: {}
      },
      paintingTable: {
        data: [],
        columns: Object.freeze(PAINTING_COLUMN)
      },
      programTable: {
        data: [],
        columns: []
      },
      paintingRes: PAINTING_RESULT
    }
  },
  components: {
    DataTable
  },
  methods: {
    print () {
      window.print()
    },
    goToIndex () {
      this.$router.push({ name: 'userinfo' })
    }
  },
  computed: {
    productStyle () {
      let styles = [1, 2, 3, 4, 5, 6, 7, 8]
      return styles.includes(parseInt(this.result.product.style))
        ? MAPS.style[parseInt(this.result.product.style) - 1]
        : this.result.product.style
    },
    productType () {
      let types = [1, 2, 3, 4, 5, 6, 7, 8, 9]
      return types.includes(parseInt(this.result.product.type))
        ? MAPS.type[parseInt(this.result.product.type) - 1]
        : this.result.product.type
    },
    productEffect () {
      let effect = parseInt(this.result.product.effect)
      if ([1, 2, 3].includes(effect)) {
        return '透明开放'
      } else if ([4, 5, 6].includes(effect)) {
        return '实色开放'
      } else if ([7, 8, 9, 10, 11].includes(effect)) {
        return '透明封闭'
      } else if ([12, 13, 14, 15, 16].includes(effect)) {
        return '实色封闭'
      } else if ([17, 18].includes(effect)) {
        return '透明仿古'
      } else {
        return '实色仿古'
      }
    },
    paintingType () {
      return MAPS.paintingType[parseInt(this.result.product.effect) - 1]
    },
    productLuster () {
      let lusters = []
      let luster = parseInt(this.result.product.luster, 2)
      let count = 1
      let currentLus = 256
      while (count < 10) {
        if (luster & currentLus) {
          if (count === 9) {
            lusters.push('亮光')
          } else {
            lusters.push(count + '分光')
          }
        }
        count++
        currentLus /= 2
      }
      return join(lusters, ', ')
    },
    productWp () {
      return MAPS.wp[parseInt(this.result.product.workpiece) - 1]
    },
    productProgram () {
      return this.result.product.program === 0 ? '手工喷涂' : '设备自动化涂装'
    }
  },
  created () {
    if (!this.$route.query.pid) {
      return this.$router.push({ name: 'userinfo' })
    }
    axios({
      method: 'post',
      url: '/client',
      // url: 'http://localhost:5000/client',
      data: { pid: this.$route.query.pid },
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(res => {
      this.result = res.data
      switch (this.result.product.effect) {
        case 1:
          this.paintingTable.data = this.paintingRes['R1']
          break
        case 2:
        case 3:
          this.paintingTable.data = this.paintingRes['R2']
          break
        case 4:
          this.paintingTable.data = this.paintingRes['R3']
          break
        case 5:
        case 6:
          this.paintingTable.data = this.paintingRes['R4']
          break
        case 7:
        case 8:
          this.paintingTable.data = this.paintingRes['R5']
          break
        case 9:
          this.paintingTable.data = this.paintingRes['R6']
          break
        case 10:
        case 11:
          this.paintingTable.data = this.paintingRes['R7']
          break
        case 12:
        case 13:
          this.paintingTable.data = this.paintingRes['R8']
          break
        case 14:
          this.paintingTable.data = this.paintingRes['R9']
          break
        case 15:
        case 16:
          this.paintingTable.data = this.paintingRes['R10']
          break
        case 17:
        case 18:
          this.paintingTable.data = this.paintingRes['R11']
          break
        case 19:
        case 20:
          this.paintingTable.data = this.paintingRes['R12']
      }
      this.paintingTable.data.map(p => {
        p.base = this.result.product.base
      })
      if (this.result.product.program === 0) {
        this.programTable.data = DRY_PROGRAM
        this.programTable.columns = Object.freeze(DRY_COLUMNS)
      } else {
        let workpiece = 'S' + this.result.product.workpiece
        this.programTable.data = DEVICE_PROGRAM[workpiece]
        this.programTable.columns = Object.freeze(DEVICE_COLUMNS)
      }
      this.loading = false
    })
  }
}
</script>
<style lang="less" scoped>
@media print {
  #no-print {
    display: none;
  }
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
h5 {
  margin: 10px 0;
  font-size: 16px;
}
.result-container {
  .flex-column;
  padding: 50px;
  text-align: left;
}
.info-container {
  p {
    line-height: 30px;
  }
}
.title {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  span {
    float: right;
  }
}
.hint {
  margin: 10px 0;
  text-align: center;
  font-size: 14px;
  color: gray;
}
img {
  margin: 0 auto;
  display: block;
}
.controller {
  text-align: center;
}
</style>



